<template>
  <!-- <div class="HomePage container mt-3 mb-3"  dir = "rtl" align = "right"> -->
  <div class="HomePage container mt-3 mb-3">
    <div v-if="fileUploaded">
      <AnnotationsPage v-bind:json="json" v-bind:prolificSubmissionId="prolificSubmissionId"/>
    </div>
    <div v-else>
      <h1>Identifying Stories Written By Humans or ChatGPT Task</h1>
      <hr>
<p>
  Your valuable input will help us gather insights and make this experiment meaningful.
</p>
  <h3>Please follow these instructions carefully:</h3>
  <ul>
  <li><b>Read the paragraph:</b> Take the time to thoroughly read and understand the provided paragraph. Pay close attention to the writing style, tone, and any unique elements that might hint at the author's identity.</li>
    <li><b>Choose the Author:</b> From the options provided, select the individual whom you believe is the most likely author of the given paragraph (ChatGPT or a Human).</li>
    <li> your instincts and reasoning while making your choice.</li>
    <li><b>Explanation:</b> After making your selection, provide a small explanation for why you believe the chosen author is the correct one. Mention specific details or writing patterns that influenced your decision. This step is crucial as it allows us to understand your thought process.</li>
    <li><b>Avoid Tainting the Experiment:</b> We kindly request that you refrain from discussing your choices with others or searching for external information about the authors while participating. This ensures that your responses are unbiased and unaffected by outside influences.</li>
    <li><b>Honest Responses:</b> Your honest and thoughtful responses are highly appreciated. Even if you are uncertain about your answer, please provide your best guess along with your reasoning. There are no right or wrong answers, and your insights are what matters most.</li>
    <li><b>Respect and Collaboration:</b> Remember that this is a collaborative effort, and we encourage open and respectful discussions about different perspectives. Engaging with others' explanations can enrich the experience and provide valuable insights.</li>
  </ul><br>
<p>
  Thank you for participating in this annotation experiment. Your contributions will aid in the analysis and interpretation of the writing styles of various authors. Let's make this a successful and enjoyable learning experience!
</p>
      <br>
      <b-button :size="''" :variant="'primary'" v-on:click="getRandomFile()">Let's Start</b-button>
    </div>
  </div>
</template>

<script>
import AnnotationsPage from "./AnnotationsPage.vue";
import { dataService } from "../services/data";
// import firebase from '../services/firebase'

export default {
  name: "HomePage",
  data: function() {
    return {
      jsonData: {
        data: [
          {
            title: "Question answering",
            paragraphs: [
              {
                context:
                  "Question answering (QA) is a computer science discipline within the fields of information retrieval and natural language processing (NLP), which is concerned with building systems that automatically answer questions posed by humans in a natural language.",
                qas: []
              },
              {
                context:
                  "A QA implementation, usually a computer program, may construct its answers by querying a structured database of knowledge or information, usually a knowledge base. More commonly, QA systems can pull answers from an unstructured collection of natural language documents.",
                qas: []
              }
            ]
          },
          {
            title: "Natural language processing",
            paragraphs: "[...]"
          }
        ]
      },
      fileUploaded: false,
      file: null,
      json: null,
      jsonID: null,
      errors: "",    
      prolificID: this.getParameterByName("PROLIFIC_PID"),
      studyID : this.getParameterByName("STUDY_ID"),
      guide:null,
      prolificSubmissionId:"",
      avoid:[]
    };
  },
    beforeMount(){
    // firebase.firestore().collection("annotations").onSnapshot((querySnapshot) => {
    //   this.avoid = [];
    //   querySnapshot.forEach((doc) => {
    //     this.avoid.push(doc.data().filename.substring(15,21));
    //   });
    // });

 },
 created() {
  const studies= {
    "62cac8bf5432ebac0556f401": "E1",
};
    const study = (this.studyID in studies)? studies[this.studyID] : "f";
    const studyDef = require(`../studies/${study}.json`);
    this.prolificSubmissionId= studyDef.prolificSubmissionId;
    // eslint-disable-next-line
    console.log(`Using study: ${study}, def:`, studyDef); 
    if (studyDef.type === "limits") {
      dataService.initializeByLimits(studyDef.min, studyDef.max);
    } else if (studyDef.type === "ids") {
      dataService.initializeByIds(studyDef.ids);
    } else {
      // eslint-disable-next-line
      console.warn(`Invalid study def type: "${studyDef.type}", using default limits`);
      dataService.initializeByLimits();
}
 
 },
  methods: {
    readFileFromUpload: function() {
      var reader = new FileReader();
      reader.onload = function(event) {
        this.json = JSON.parse(event.target.result);
        this.fileUploaded = true;
        
      }.bind(this);
      reader.readAsText(this.file);
    },
    guideClicked: function () {
      this.guide = "pass"
    },
    checkID: function(){
      this.errors = ""
      return true;
      // eslint-disable-next-line no-console
      // console.log(this)
      // if(this.prolificID == ""){
      //   this.errors = "Must ";
      //   return false;
      // }
      // else if(this.guide !== "pass" && this.prolificID !== "Roi"){
      //     this.errors = "חובה לקרוא את ההנחיות לפני שמתחילים";
      //   return false
      // }
      // else{
      //     this.errors = "";
      //     return true;
      // }
    },
    getRandomFile: async function () {// When pressing the button!!!
      if (!this.checkID()) {
        return;
      }
      this.jsonID = dataService.getNextId();
      const paddedID = this.checkFile(dataService.pad(this.jsonID, 7));
      this.json = require(`../json_resources/gpt-tagging${paddedID}.json`);
      this.json.jsonID = this.jsonID;
      this.json.prolificID = this.prolificID;
      this.json.studyID = this.studyID;
      this.fileUploaded = true;
    },
    getParameterByName: function (name) {
      let queryDict = {};
      location.search.substr(1).split("&").forEach(function (item) {
        queryDict[item.split("=")[0]] = item.split("=")[1]
      });
      if (name in queryDict) {
        return queryDict[name];
      }
      return "";
    },
    state: function (jsonID) {
      var intID = parseInt(jsonID)
      return intID > 0 && intID < 1200;
    },
    checkFile: function (paddedID) {
      // eslint-disable-next-line no-console
      // console.log(paddedID)
      // eslint-disable-next-line no-console
      // console.log(this.avoid)
      let i = 0;
      if(this.avoid.includes(paddedID) && i<100)
      {
        paddedID =  dataService.pad(dataService.getNextId() , 7);
        i++;
        // eslint-disable-next-line no-console
        // console.log(this.avoid)
      }
      return paddedID
    }
    
  },
  components: {
    AnnotationsPage
  }
};
</script>

